import React from "react";
import Layout from '../../components/Global/Layout/Layout'
import HeaderHome6 from "../../components/Local/Home/HomeSix/Header.HomeSix.component";
import BannerHomeSix from "../../components/Local/Home/HomeSix/Banner.HomeSix.component";
import FeaturedProperty6 from "../../components/Local/Home/HomeSix/FeaturedProperty.HomeSix.component";
import PropertyCitiesHome6 from "../../components/Local/Home/HomeSix/PropertyCities.HomeSix.component";
import OurAgentsHome6 from "../../components/Local/Home/HomeSix/OurAgents.HomeSix.component";
import OurBlogComponent from "../../components/Global/Home/OurBlog/OurBlog.component";
import OurPartners from "../../components/Global/OurPartners/OurPartners.component";
import FooterHome6 from "../../components/Local/Home/HomeSix/Footer.HomeSix.component";
import { Link } from "gatsby";

const Home6 = () => {
	return (
		<Layout>
		 	<HeaderHome6/>
			<BannerHomeSix/>
            <FeaturedProperty6/>
            <section className="you-looking-for">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 offset-lg-3">
                            <div className="main-title text-center mb30">
                                <h2>What are you looking for?</h2>
                                <p>We provide full service at every step.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <PropertyCitiesHome6/>
            <section className="modern-apertment pt100 pb90">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="modern_apertment mt30">
                                <h2 className="title">Modern Apartment</h2>
                                <h4 className="subtitle">$79 at night</h4>
                                <p>I am text block. Click edit button to change this text. Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
                                <Link className="btn booking_btn btn-thm" to="#">Book Now</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <OurAgentsHome6/>
            <OurBlogComponent/>
            <OurPartners/>
            <FooterHome6/>
		</Layout>
	);
}

export default Home6